:root {
  --color-primari: #5fbaa7;
  --color-primari-transp: #eff8f6;
  --color-secundari: #242333;
  --color-blanc: #fff;
  --color-gris: #444451;
}

html {
  height: 100%;
  color: var(--color-secundari);
  font-family: Inter, sans-serif;
  font-size: 14px;
  font-weight: 400;
}

body {
  margin: 0;
}

html h2 {
  color: var(--color-primari);
}

ul {
  list-style-type: none;
}

header {
  background-color: var(--color-gris);
  color: var(--color-blanc) !important;
}

header a {
  border-bottom: initial;
  text-decoration: none;
  color: var(--color-blanc) !important;
}

header a:hover {
  border-bottom: initial;
  text-decoration: none;
  color: var(--color-primari) !important;
}

body.portada {
  height: 100%;
  background-image: url("oliva.422dd6e8.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.portada > main {
  text-align: center;
  display: block;
}

.btn {
  color: var(--color-blanc);
  background-color: var(--color-primari);
  text-align: center;
  border: none;
  border-radius: 12px;
  margin: 1rem;
  padding: 1rem;
  font-weight: 700;
}

.btn:hover, .btnIndex {
  background-color: var(--color-blanc);
  color: var(--color-primari);
  text-align: center;
  border: none;
  border-radius: 12px;
  margin: 1rem;
  padding: 1rem;
  font-weight: 700;
}

.btnIndex > a {
  color: var(--color-primari);
  border-bottom: initial;
  text-decoration: none;
}

.btnIndex > a:hover {
  color: var(--color-gris);
  border-bottom: initial;
  text-decoration: none;
}

@media screen and (max-width: 43rem) {
  .container {
    min-height: 100vh;
    position: relative;
  }

  .marge {
    margin-left: 2%;
    margin-right: 2%;
  }

  .col_2 {
    grid-template-columns: 1fr;
    grid-auto-rows: auto;
    gap: 0;
    display: grid;
  }

  .row {
    grid-template-columns: 1fr;
    grid-auto-rows: auto;
    gap: 10px;
    display: grid;
  }

  .col_4 {
    max-width: 100%;
    grid-template-columns: 1fr;
    grid-auto-rows: minmax(100px, auto);
    gap: 20px;
    display: grid;
  }

  #logotip {
    max-width: 30%;
  }
}

@media screen and (min-width: 44rem) and (max-width: 62rem) {
  .container {
    min-height: 100vh;
    position: relative;
  }

  .marge {
    margin-left: 10%;
    margin-right: 10%;
  }

  .col_2 {
    grid-template-columns: 1fr;
    grid-auto-rows: auto;
    gap: 0;
    display: grid;
  }

  .col_4 {
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: minmax(100px, auto);
    gap: 20px;
    display: grid;
  }

  #logotip {
    max-width: 20%;
  }
}

@media screen and (min-width: 63rem) {
  .container {
    position: relative;
  }

  .marge {
    margin-left: 20%;
    margin-right: 20%;
  }

  .col_2 {
    grid-template-columns: 1fr 2fr;
    grid-auto-rows: minmax(50px, auto);
    gap: 0;
    margin-bottom: 5%;
    display: grid;
    position: relative;
  }

  .col_4 {
    max-width: 100%;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-auto-rows: minmax(100px, auto);
    gap: 20px;
    display: grid;
  }

  .menu > li {
    padding-right: 2%;
    display: inline-flex;
  }

  .menu_inicial {
    width: 60%;
    color: var(--color-secundari);
    position: absolute;
    top: 60%;
    left: 70%;
    transform: translate(-50%, -50%);
  }

  #logotip {
    max-width: 40%;
  }

  .categoria > footer {
    background-color: var(--color-gris);
    margin-top: 5%;
    padding: 1.5rem;
    font-size: 1.1rem;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    color: var(--color-blanc) !important;
  }
}

footer, .categoria > footer {
  background-color: var(--color-gris);
  margin-top: 5%;
  padding: 1.5rem;
  font-size: 1.1rem;
  position: absolute;
  left: 0;
  right: 0;
  color: var(--color-blanc) !important;
}

.portada > footer {
